@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap");

html {
  margin: 10px;
  font-family: "Noto Sans JP", sans-serif;
}

/* Event Handling (Table Description [employee]) */

table,
td,
th {
  border: 2px solid rgb(49, 0, 49);
  padding: 20px;
  text-align: center;
  font: italic bold 20px/1.6 cursive;
}

td {
  border: none;
}

th {
  color: white;
  background-color: #800040;
}

td:nth-child(2) {
  font-variant: small-caps;
}

table {
  border: 6px double lightgreen;
  width: 600px;
  margin: auto;
  border-radius: 30px;
  background-color: #f2f2f2;
  padding: 20px;
  margin: auto;
  width: 70%;
}

tr:nth-child(even) {
  background-color: rgb(210, 210, 210);
}

tr:nth-child(odd) {
  background-color: white;
}

.remove-button {
  background: url("./icons/close-bin.png") center no-repeat;
  background-size: contain;
  padding: 12px;
  display: inline-block;
  border: none;
  cursor: pointer;
}

.remove-button:hover {
  background-image: url("./icons/open-bin.png");
}
/* Event Handling (a small illustration) */

.add-button {
  background: url("./icons/add.png") center no-repeat;
  background-size: contain;
  padding: 15px;
  vertical-align: middle;
  display: inline-block;
  border-radius: 50%;
  background-color: white;
}

.cancel {
  transform: rotate(45deg);
  background-color: red;
}

.button-container {
  display: inline;
  width: 15rem;
  margin: 30px calc(50% - 15rem / 2);
  border: 1px solid black;
  padding: 13px;
  text-align: center;
  display: inline-block;
  color: white;
  background-color: #800040;
  border-radius: 20px;
  cursor: pointer;
}

.text {
  margin-left: 20px;
  vertical-align: middle;
  user-select: none;
  font-size: large;
  display: inline-block;
}

p.text {
  display: block;
  text-align: center;
  margin: 10px auto 20px auto;
  color: white;
  font-size: 22px;
  font-variant: small-caps;
  font-weight: bold;
  letter-spacing: 2px;
}

input[type="text"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.form button {
  width: 100%;
  background-color: #4caf50;
  color: white;
  padding: 14px 20px;
  border: none;
  cursor: pointer;
  display: block;
  width: 50%;
  margin: 20px auto;
}

.form button:hover {
  background-color: #45a049;
}

.show-form {
  border-radius: 30px;
  background-color: #f2f2f2;
  background-color: rgb(112, 112, 112);
  padding: 20px;
  margin: auto;
  width: 44%;
}

.form {
  width: 50%;
  margin: auto;
}
